.footer {
    padding-bottom: 30px;
    background-color: $grey-bg;
    &__inner {
        text-align: center;
        &::before {
            content: "";
            display: block;
            width: 100%;
            height: 2px;
            background-color: $grey-separator;
            margin-bottom: 30px;
        }
    }
    &__adress {
        font-size: 16px;
        line-height: normal;
        color: $main-regular;
        padding-bottom: 10px;
    }
    &__map-link {
        color: $active-regular;
        font-size: 12px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 1px;
        text-transform: uppercase;
        transition-duration: 300ms;
        transition-property: color;
        &:hover {
            color: $accent;
        }
    }
    &__contacts {
        padding-top: 21px;
        padding-bottom: 30px;
        display: grid;
        grid-gap: 10px;
        justify-items: center;
    }
    &__fb-svg .icon-color {
        transition-duration: 300ms;
        transition-property: fill;
    }
    &__fb:hover &__fb-svg .icon-color {
        fill: $accent;
    }
    &__phone {
        color: $main-alt;
    }
    &__mail {
        color: $active-regular;
        font-size: 16px;
        font-weight: 700;
        line-height: normal;
        transition-duration: 300ms;
        transition-property: color;
        &:hover {
            color: $accent;
        }
    }
    &__copy {
        font-size: 16px;
        line-height: normal;
        color: $grey-text;
    }
    &__map-iframe-wrap {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.4);
        padding: 0 31px;
        display: none;
        align-items: center;
        justify-content: center;
        animation-name: hidden;
        animation-duration: 200ms;
        animation-fill-mode: both;
        &.visible {
            z-index: 99;
            display: flex;
            animation-delay: 300ms;
            animation-name: visible;
        }
    }
    &__map-iframe-inner {
        width: 100%;
        height: 70%;
        border: 3px solid $main-alt;
        box-shadow: 0 0px 3px 3px rgba(50, 50, 100, 0.8);
        position: relative;
    }
    &__map-iframe-btn {
        cursor: pointer;
        border: none;
        background: $basic-white;
        padding: 0;
        font-size: 0;
        position: absolute;
        right: 0px;
        top: 0;
        padding: 4px;
        box-shadow: 0 0px 3px 4px rgba(50, 50, 100, 0.8);
        border-radius: 0 0 0 4px;
    }
    &__map-iframe-btn:hover &__map-iframe-close .icon-color {
        stroke: $basic-black;
    }
    @keyframes hidden {
        0% {
            display: flex;
            opacity: 1;
            visibility: visible;
        }
        100% {
            display: none;
            opacity: 0;
            visibility: hidden;
        }
    }
    @keyframes visible {
        0% {
            display: flex;
            opacity: 0;
            visibility: hidden;
        }
        100% {
            display: flex;
            opacity: 1;
            visibility: visible;
        }
    }
}

@media (min-width: 768px) {
    .footer {
        &__inner {
            display: flex;
            flex-wrap: wrap;
        }
        &__adress-box {
            display: flex;
        }
        &__adress {
            padding-bottom: 0;
            padding-right: 10px;
        }
        &__contacts {
            grid-auto-columns: max-content;
            grid-auto-flow: column;
            justify-items: start;
            order: 1;
            width: 100%;
            padding-bottom: 0;
        }
        &__fb-svg .icon-color {
            transition-duration: 300ms;
            transition-property: fill;
        }
        &__fb:hover &__fb-svg .icon-color {
            fill: #f0145a;
        }
        & .dot-separ::before {
            background-color: $grey-text;
            margin-right: 10px;
        }
        &__copy {
            margin-left: auto;
        }
    }
}

@media (min-width: 768px) and (max-width: 1023.98px) {
    .footer_content-center {
        .footer__inner {
            justify-content: center;
        }
        .footer__adress-box {
            display: flex;
            justify-content: center;
        }
        .footer__contacts {
            order: 0;
            justify-content: center;
        }
        .footer__copy {
            margin-left: 0;
            padding-top: 30px;
        }
    }
}
